// Colours
$default-text-color: rgba(175, 175, 175, 1);
$bronze: rgba(80,50,20,1);
$white: rgba(244, 244, 244, 1);

// Media Queries
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin tablet {
  @media (min-width: #{$tablet-width}) {
    @content; } }

@mixin desktop {
  @media (min-width: #{desktop-width}) {
    @content; } }
